.Home{
    padding:1cm;
    width:100vw;
    height:95vh;
    background-image: url('background.jpg');
    background-size: cover;
    color: rgb(61, 61, 61);
    font-size: 20px;
    font-weight: bold;
}

.container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    row-gap: 1cm;
}

.temp{
    display: flex;
    flex-direction: row;
    column-gap: 0.5cm;
}  

.min, .max{
    display: flex;
    flex-direction: row;
}  